        <span class="hljs-attr">material1</span>: {},

        <span class="hljs-attr">rectangle2</span>: {},
        <span class="hljs-attr">coordinates2</span>: { <span class="hljs-attr">west</span>: <span class="hljs-number">-110</span>, <span class="hljs-attr">south</span>: <span class="hljs-number">30</span>, <span class="hljs-attr">east</span>: <span class="hljs-number">-100</span>, <span class="hljs-attr">north</span>: <span class="hljs-number">40</span> },
        <span class="hljs-attr">material2</span>: {},
        <span class="hljs-attr">rotation2</span>: {},
        <span class="hljs-attr">outlineColor2</span>: {},

