        <span class="hljs-attr">coordinates3</span>: { <span class="hljs-attr">west</span>: <span class="hljs-number">-92.0</span>, <span class="hljs-attr">south</span>: <span class="hljs-number">30.0</span>, <span class="hljs-attr">east</span>: <span class="hljs-number">-82.0</span>, <span class="hljs-attr">north</span>: <span class="hljs-number">40.0</span> },
        <span class="hljs-attr">material3</span>: {},
        <span class="hljs-attr">rotation3</span>: {},
        <span class="hljs-attr">stRotation3</span>: {},
        <span class="hljs-attr">classificationType3</span>: <span class="hljs-number">0</span>
      }
    },
    mounted() {
      <span class="hljs-built_in">Promise</span>.all([
        <span class="hljs-keyword">this</span>.$refs.rectangle1.createPromise,
        <span class="hljs-keyword">this</span>.$refs.rectangle2.createPromise,
        <span class="hljs-keyword">this</span>.$refs.rectangle3.createPromise
      ]).then(<span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
        instances[<span class="hljs-number">0</span>].viewer.zoomTo(instances[<span class="hljs-number">0</span>].viewer.entities)
      })
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">this</span>.rotation = Cesium.Math.toRadians(<span class="hljs-number">30</span>)
        <span class="hljs-keyword">this</span>.material1 = Cesium.Color.RED.withAlpha(<span class="hljs-number">0.5</span>)

        <span class="hljs-keyword">this</span>.material2 = Cesium.Color.GREEN.withAlpha(<span class="hljs-number">0.5</span>)
        <span class="hljs-keyword">this</span>.rotation2 = Cesium.Math.toRadians(<span class="hljs-number">45</span>)
        <span class="hljs-keyword">this</span>.outlineColor2 = Cesium.Color.BLACK

        <span class="hljs-keyword">this</span>.material3 = <span class="hljs-string">'https://zouyaoji.top/vue-cesium/favicon.png'</span>
        <span class="hljs-keyword">this</span>.outlineColor3 = <span class="hljs-string">`BLACK`</span>
